interface SolarLogoProps {
  className?: string
  size?: "sm" | "md" | "lg"
  showText?: boolean
}

export function SolarLogo({ className = "", size = "md", showText = true }: SolarLogoProps) {
  const sizes = {
    sm: { icon: 32, text: "text-lg" },
    md: { icon: 40, text: "text-xl" },
    lg: { icon: 56, text: "text-3xl" },
  }

  const { icon, text } = sizes[size]

  return (
    <div className={`flex items-center gap-2 ${className}`}>
      {/* Sun icon matching the logo */}
      <svg
        width={icon}
        height={icon}
        viewBox="0 0 100 100"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="flex-shrink-0"
      >
        {/* Sun rays */}
        <g stroke="#EA580C" strokeWidth="6" strokeLinecap="round">
          {/* Top ray */}
          <line x1="50" y1="8" x2="50" y2="22" />
          {/* Top-right ray */}
          <line x1="75" y1="18" x2="68" y2="30" />
          {/* Right ray */}
          <line x1="92" y1="50" x2="78" y2="50" />
          {/* Bottom-right ray */}
          <line x1="75" y1="82" x2="68" y2="70" />
          {/* Bottom ray */}
          <line x1="50" y1="92" x2="50" y2="78" />
          {/* Bottom-left ray */}
          <line x1="25" y1="82" x2="32" y2="70" />
          {/* Left ray */}
          <line x1="8" y1="50" x2="22" y2="50" />
          {/* Top-left ray */}
          <line x1="25" y1="18" x2="32" y2="30" />
        </g>
        {/* C-shaped sun body (open on right side) */}
        <path
          d="M68 50C68 59.9411 59.9411 68 50 68C40.0589 68 32 59.9411 32 50C32 40.0589 40.0589 32 50 32C59.9411 32 68 40.0589 68 50"
          stroke="#EA580C"
          strokeWidth="6"
          strokeLinecap="round"
          fill="none"
          transform="rotate(90, 50, 50)"
        />
      </svg>
      {showText && (
        <span className={`font-semibold tracking-wide ${text}`}>
          <span className="text-primary">SOLAR</span>
          <span className="text-foreground">Quote</span>
        </span>
      )}
    </div>
  )
}
